<script>
export default {
  data() {
    return {
      swiperOptions: {
        autoplay: true,
        effect: 'fade',
        speed: 500,
        delay: 500,
        loop: true,
        loopPreventsSlide: true,
      }
    }
  },
  methods: {
    Wow() {
      let wow = new this.$Wow.WOW({
        boxClass: 'wow',
        animateClass: 'animate__animated',
        offset: 0,
        mobile: true,
        live: true
      });
      wow.init();
    }
  },
  mounted() {
    this.Wow()
  }
}
</script>

<template>
